.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sub_div {
  transform: translate(0, 30px);
  margin-top: 15px;
  display: flex;
}

.main_title {
  margin: auto;
  border: solid 4px;
  border-radius: 10px;
  color: royalblue;
  font-family: "Avenir";
  font-weight: bold;
  text-align: center;
  width: 700px;
}

#charges, #stats {
  border: 2px solid;
  width: 600px;
  height: 200px;
  border-radius: 10px;
  margin-left: 30px;
  margin-right: 30px;
  border-color: dodgerblue;
}

form > input {
  margin-left: 80%;
  transform: translate(0, -30px);
}
